.color {
  color: #3ee9e5;
}

.month {
  font-size: 1.125rem;
  opacity: 0.6;
  font-weight: 400;
}

.error {
  border: 2px solid #ff2965 !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: #080c20;
}

h1,
h2,
h3,
h4 {
  color: #fff;
}

p {
  color: #777f98;
}

header {
  max-width: 500px;
  margin: 0 auto;
}
@media screen and (min-width: 590px) {
  header {
    position: relative;
    max-width: 100vw;
  }
}
@media screen and (min-width: 1200px) {
  header {
    max-width: 1500px;
    margin: 0 auto;
  }
}

header,
section,
footer {
  padding: 0 1rem;
}
@media screen and (min-width: 590px) {
  header,
section,
footer {
    padding: 0;
  }
}

section,
footer {
  margin-top: 9rem;
}
@media screen and (min-width: 590px) {
  section,
footer {
    max-width: 560px;
    margin: 9rem auto 0;
  }
}

footer {
  margin-bottom: 5rem;
}

@media screen and (min-width: 1200px) {
  main {
    max-width: 1100px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1200px) {
  .logo-container,
section {
    max-width: 1100px;
  }
}

body {
  font-family: "Manrope", sans-serif;
  line-height: 1.5;
}

h1 {
  font-size: 2rem;
}
@media screen and (min-width: 1200px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.125rem;
}

h3 {
  font-size: 1.5rem;
}
@media screen and (min-width: 1200px) {
  h3 {
    font-size: 2rem;
  }
}

h4 {
  font-size: 3rem;
  margin: 1.5rem auto 1.25rem;
}

p {
  line-height: 1.8;
}

p,
button,
input,
::placeholder {
  font-family: inherit;
  font-size: 1rem;
}

section,
footer,
header {
  text-align: center;
}

.benefits__card {
  max-width: 400px;
}
.benefits__card:first-child img {
  margin-right: -0.7rem;
}
.benefits__card:nth-child(2) img {
  width: 60px;
}
.benefits__card:nth-child(4) img {
  width: 78px;
}
.benefits__card--img-container {
  height: 124px;
  width: 120px;
  margin: 0 auto;
  background-color: #093f68;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 45px;
}
@media screen and (min-width: 590px) {
  .benefits__card--img-container {
    height: 124px;
    width: 165px;
  }
}
@media screen and (min-width: 1200px) {
  .benefits__card--img-container {
    width: 160px;
    height: 164px;
    margin-left: 0;
  }
}
.benefits__card--img-container img {
  width: 70px;
}
@media screen and (min-width: 590px) {
  .benefits__card--content {
    text-align: left;
  }
}
.benefits__card--content__title {
  margin: 2rem auto 0.687rem;
}
@media screen and (min-width: 590px) {
  .benefits__card--content__title {
    margin: 1rem auto auto;
  }
}

.pricing__plans--card {
  border-radius: 8px;
  text-align: left;
  padding: 0 1.5rem 1.5rem;
}
@media screen and (min-width: 590px) {
  .pricing__plans--card {
    max-width: 445px;
  }
}
.pricing__plans--card__title {
  margin-top: 1.5rem;
}
.pricing__plans--card__top {
  margin-top: -1.4rem;
}
.pricing__plans--card p {
  color: #fff;
}
.pricing__plans--card__para {
  color: #fff;
  opacity: 0.7;
}
.pricing__plans--card.free {
  background-color: #093f68;
}
.pricing__plans--card.free svg path {
  fill: #3ee9e5;
}
.pricing__plans--card.premium {
  background-color: #3ee9e5;
}
.pricing__plans--card.premium h2,
.pricing__plans--card.premium h4,
.pricing__plans--card.premium p {
  color: #080c20;
}
.pricing__plans--card.premium__para {
  opacity: 0.6;
}
.pricing__plans--card.premium svg path {
  fill: #080c20;
}

.logo-container {
  margin: 2.5rem auto 4.125rem;
}
@media screen and (min-width: 590px) {
  .logo-container {
    margin: 3rem auto 4.812rem 2.5rem;
    text-align: left;
  }
}
@media screen and (min-width: 1200px) {
  .logo-container {
    margin: 3rem auto 7.125rem;
  }
}

.background {
  background: url(../assets/illustration-hero-mobile.png);
  height: 270px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  max-width: 480px;
  margin: 0 auto;
}
@media screen and (min-width: 360px) {
  .background {
    background-position: left top;
    background-size: 109%;
  }
}
@media screen and (min-width: 768px) {
  .background {
    display: grid;
    grid-template-columns: 1fr 3fr 0.8fr;
    gap: 1rem;
    max-width: 100vw;
    min-height: 450px;
    background: none;
  }
  .background > div:nth-child(1) {
    background: url(../assets/illustration-hero-left.svg);
    background-position: top right;
    background-repeat: no-repeat;
  }
  .background > div:nth-child(2) {
    background: url(../assets/bg-hero-squiggle.svg);
    background-size: cover;
  }
  .background > div:nth-child(3) {
    background: url(../assets/illustration-hero-right.svg);
    background-position: top left;
    background-repeat: no-repeat;
  }
}
@media screen and (min-width: 1200px) {
  .background {
    min-height: 550px;
    grid-template-columns: 2.5fr 5fr 2.5fr;
  }
}

@media screen and (min-width: 590px) {
  .heading__content {
    max-width: 480px;
    margin: 0 auto;
  }
}
@media screen and (min-width: 768px) {
  .heading__content {
    max-width: 560px;
    position: absolute;
    top: 56%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media screen and (min-width: 1200px) {
  .heading__content {
    max-width: 660px;
  }
}
.heading__content--title {
  margin: 3rem auto 1.5rem;
  padding: 0 1.5rem;
}
@media screen and (min-width: 590px) {
  .heading__content--title {
    padding: 0 3rem;
  }
}
.heading__content--para {
  margin-bottom: 2rem;
}
@media screen and (min-width: 1200px) {
  .heading__content--para {
    margin: 1.5rem auto 4rem;
  }
}

.benefits {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.4rem;
  max-width: 560px;
  min-height: 425px;
}
@media screen and (min-width: 1200px) {
  .benefits {
    max-width: 1100px;
    flex-direction: row;
    gap: 1.95rem;
  }
}
@media screen and (min-width: 590px) {
  .benefits__card {
    display: flex;
    align-items: center;
    gap: 3.4rem 2.5rem;
    max-width: 515px;
  }
  .benefits__card:nth-child(odd) {
    margin-left: auto;
  }
  .benefits__card:nth-child(even) {
    margin-right: auto;
  }
}
@media screen and (min-width: 1200px) {
  .benefits__card {
    flex-direction: column;
  }
  .benefits__card:nth-child(odd) {
    margin-bottom: auto;
  }
  .benefits__card:nth-child(even) {
    margin-top: auto;
  }
}

.pricing__plans--card__benefits--row {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.95rem;
}
.pricing__plans--card__benefits--row p {
  margin: 0.5rem 0;
}

.pricing__content {
  max-width: 540px;
  margin: 0 auto;
}
.pricing__content--para {
  margin: 1.575rem auto 4.5rem;
}
@media screen and (min-width: 1200px) {
  .pricing__content--para {
    margin: 1.575rem auto 7rem;
  }
}
.pricing__plans {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
}
@media screen and (min-width: 1200px) {
  .pricing__plans {
    flex-direction: row;
    justify-content: center;
    gap: 1.8rem;
  }
}

.pricing__plans--table__title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

@media screen and (min-width: 590px) {
  .subscribe {
    max-width: 100vw;
    background: url("../assets/bg-footer-squiggle.svg") center/120% 100% no-repeat;
    padding: 2rem 0;
  }
  .subscribe > * {
    max-width: 480px;
    margin: 0 auto;
  }
}
.subscribe__title {
  background: url("../assets/bg-footer-squiggle.svg") center/120% 100% no-repeat;
  padding: 1rem 3.5rem;
}
@media screen and (min-width: 590px) {
  .subscribe__title {
    padding: 2rem 0;
    background: none;
  }
}
.subscribe form {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 590px) {
  .subscribe form {
    flex-direction: row;
    align-items: center;
    gap: 1rem;
  }
}
.subscribe form .input-mail__container {
  position: relative;
  padding-bottom: 1.8rem;
}
@media screen and (min-width: 590px) {
  .subscribe form .input-mail__container {
    padding: 0;
    flex: 1;
  }
}
.subscribe form .input-mail__container p {
  position: absolute;
  font-size: 0.75rem;
  bottom: -7px;
  left: 0.75rem;
  color: #ff2965;
  display: none;
}
@media screen and (min-width: 590px) {
  .subscribe form .input-mail__container p {
    bottom: -36px;
  }
}
.subscribe form .input-mail__container input[type=email] {
  background-color: #093f68;
  border: 0;
  padding: 0.675rem 1rem;
  border-radius: 1.5rem;
  color: #fff;
  font-weight: 500;
  width: 100%;
}
.subscribe form .input-mail__container input[type=email] ::placeholder {
  color: #fff;
  opacity: 0.5;
}
.subscribe form .input-mail__container input[type=email]:focus {
  outline: 2px solid #3ee9e5;
}
.subscribe form button[type=submit] {
  background-color: #3ee9e5;
  border: 0;
  padding: 0.675rem 1rem;
  border-radius: 1.5rem;
  color: #080c20;
  font-weight: 500;
  border: 2px solid transparent;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.subscribe form button[type=submit] ::placeholder {
  color: #080c20;
  opacity: 0.5;
}
.subscribe form button[type=submit]:hover, .subscribe form button[type=submit]:focus {
  background-color: #080c20;
  color: #fff;
  border: 2px solid #3ee9e5;
}
@media screen and (min-width: 590px) {
  .subscribe form button[type=submit] {
    max-width: 140px;
  }
}
.subscribe form button[type=submit]:focus {
  outline: none;
}